import {SCREEN_MUTATIONS, ScreenState} from "../modules/screen";
import {Store} from 'vuex';
import {CLIENT_MUTATIONS} from "../modules/client";

interface StateInterface {
    screen: ScreenState
};

interface MutationInterface {
    payload: any,
    type: string
}

export const SynchronizationPlugin = (store: Store<any>) => {
    let initState = localStorage.getItem('state');
    if (initState) {
        let root_state = JSON.parse(initState);
        let screen_state = root_state.screen;
        let client_state = root_state.client;

        store.commit(SCREEN_MUTATIONS.setInitState, screen_state || {});
        store.commit(CLIENT_MUTATIONS.setInitState, client_state || {});
    }

    store.subscribe((mutation: MutationInterface, state: StateInterface) => {
        localStorage.setItem('state', JSON.stringify(state));
    });
};
