import {ActionContext} from "vuex";
import {User} from '@/core/entities/user/model';
import {ScreenState} from "./screen";

export const CLIENT_MUTATIONS = {
    setInitState: 'setClientInitState',
    setClient: 'setClientClient',
};

export const CLIENT_ACTIONS = {
    clearClient: 'clearClient'
};

export interface ClientState {
    model?: User
}

let state: ClientState = {
    model: undefined
};

export default {
    state: state,
    mutations: {
        [CLIENT_MUTATIONS.setInitState]: (state: ClientState, initState: ClientState) => {
            Object.assign(state, initState);
        },
        [CLIENT_MUTATIONS.setClient]: (state: ClientState, client: User) => {
            state.model = client;
        }
    },
    getters: {
        getClient: (state: ClientState): User | undefined => state.model,
    },
    actions: {
        [CLIENT_ACTIONS.clearClient]: (context: ActionContext<ClientState, any>) => {
            return new Promise((resolve, reject) => {
                context.commit(CLIENT_MUTATIONS.setClient, undefined);
            });
        }
    }
};
